<template>
  <div>
    <v-container>
      <div class="row gx-0">
        <v-col cols="12">
          <div class="mobile">
            <carousel v-if="features.length > 0" :autoplay="false" :margin="30" :items="4" :dots="false" :slideBy="1"
                    :navText="['‹','›']"
                    :responsive = "{0:{items:1},576:{items:2},768:{items:3},1200:{items:4}}" class="deals mobile">
            <div v-for="(feature,i) in features" :key="i" class="item mitem my-2">
              <div class="small d-flex">
                <router-link :to="{name:'businessprofile',params:{id:feature.user_id, uname:feature.name}}" class="link-dark text-decoration-none">
                  <div class="mimg">
                    <v-img v-if="feature.user_avatar==!null" cover :src=url+feature.user_avatar height="110" class="rounded-3 white--text align-start"
                         gradient="to bottom, rgba(0,0,0,.1), rgba(0,0,0,.5)">
                    </v-img>
                    <v-img v-if="feature.user_avatar==null" cover :src=url+default_image_feature height="110" class="rounded-3 white--text align-start"
                         gradient="to bottom, rgba(0,0,0,.1), rgba(0,0,0,.5)">
                    </v-img>
                  </div>
                </router-link>
                <div class="feat badge bgred">Featured</div>
                  <div class="mdetails">
                    <router-link :to="{name:'businessprofile',params:{id:feature.user_id, uname:feature.name}}" class="link-dark">
                        <div class="h6 p-0">{{ feature.name }}</div>
                    </router-link>
                    <v-icon small>mdi-map-marker</v-icon>
                    {{feature.city}}, {{feature.state}}
                  </div>
             
              </div>
              
            </div>
          </carousel>
        </div>
          <div class="desktop">
            <carousel v-if="features.length > 0" :autoplay="false" :margin="30" :items="4" :dots="false" :slideBy="1"
                    :navText="['‹','›']" :responsive = "{0:{items:1},576:{items:2},768:{items:3},1200:{items:4}}" class="deals">
              <div v-for="(feature,i) in features" :key="i" class="item">
                <router-link :to="{name:'businessprofile',params:{id:feature.user_id, uname:feature.name}}" class="link-dark text-decoration-none">
                  <v-card>
                    <v-img v-if="feature.user_avatar==!null" cover :src=url+feature.user_avatar height="150"
                          class="white--text align-start"
                          gradient="to bottom, rgba(0,0,0,.1), rgba(0,0,0,.5)">
                    </v-img>
                    <v-img v-if="feature.user_avatar==null" cover :src=url+default_image_feature height="150"
                          class="white--text align-start"
                          gradient="to bottom, rgba(0,0,0,.1), rgba(0,0,0,.5)">
                    </v-img>

                    <div class="feat badge bgred">Featured</div>
                    <v-card-text>
                        <router-link :to="{name:'businessprofile',params:{id:feature.user_id, uname:feature.name}}" class="link-dark">
                            <div class="h6">{{ feature.name }}</div>
                        </router-link>
                      <v-icon small>mdi-map-marker</v-icon>
                      {{feature.city}}, {{feature.state}}
                    </v-card-text>
                  </v-card>
                </router-link>
              </div>
          </carousel>
        </div>
        </v-col>
      </div>
    </v-container>
    <DailyDeals/>

  </div>
</template>

<script>
import carousel from "vue-owl-carousel";
import DailyDeals from "@/views/deals/DailyDeals";
import axios from "axios";
export default {
  name: "AllDeals",
  components:{DailyDeals, carousel},
  data (){
    return{
      url: 'https://bizlx.itechvision.in',
      default_image_feature: "/images/info-1.jpg",
      features: [],

    }
  },
  mounted(){
    this.jobTypes();
  },
  methods: {
      jobTypes() { // get all feature
          axios.get("business/featured")
              .then((resp) => {
                  this.features = resp.data.businessFeature;
              }
          );
      },
  }
}
</script>

