  left:8px;
  font-weight: 400;
}
.item.mitem {
  box-shadow: 0 6px 10px -6px rgba(0,0,0,0.5);
  background: #f1f2f380;
  padding: 7px;
}
.mimg {
  width: 150px;
}
.mtitle {
  font-weight: 600;
  font-size: 12.25px;
}
.mdetails {
  width: 100%;
  padding: 0px 0 0 7px;
}
.mcontact {
  width: 42px;
}
.fs11{font-size: 10.4px;}

@media(max-width: 576px){
  .feat {
  position: absolute;
  top: 20px;
